.section__img-wrap {
  background: $upload;
  min-width: 320px;
  @media (min-width: 768px) {
    min-width: 368px;
  }

  @media (min-width: 1360px) {
    min-width: 670px;
    height: 460px;
  }
}

.section__picture {
  width: 100%;
  height: 100%;
  display: flex;
}

.section__img {
  object-fit: cover;
}
